@-webkit-keyframes change {
  0% {
    background-color: rgb(225, 221, 2);
  }
  25% {
    background-color: #af2eff;
  }
  50% {
    background-color: rgb(27, 250, 27);
  }
  70% {
    background-color: rgb(1, 173, 173);
  }
  75% {
    background-color: white;
  }
  100% {
    background-color: rgb(225, 221, 2);
  }
}

@keyframes change {
